.actionText {
  margin: 0 auto 20px auto;
  color: white;
  padding-left: 18px;
  font-size: 14px;
}

@media all and (min-width: 800px) {
  .actionText {
    color: #0f0f0f;
  }
}

.actions {
  line-height: 1.25;
}

.actionButtons {
  font-size: 22px;
  position: relative;
  text-align: center;
  max-width: 420px;
  margin: 40px auto 30px auto;
}

.actionButton {
  display: inline;
  font-size: 24px;
  position: relative;
  margin: 0 4px;
  padding: 8px 20px;
  height: 40px;
  background: #005060;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.25);
  color: #f5f5f5;
  border-radius: 0;
  cursor: pointer;
}

.actionButton:focus {
  outline: none;
}

.actionButton:hover {
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.5);
}

.goMatch, .goMatch:visited {
  cursor: pointer;
  display: inline-block;
  margin: 0 auto;
  background: #005060;
  text-decoration: none;
  color: white;
  padding: 10px 20px;
  box-shadow: 0 0 12px 0 rgba(240, 240, 240, 0.3), 0 0 12px 0 rgba(240, 240, 240, 0.3);
}

.goMatch:hover {
  box-shadow: 2px 2px 32px 0 rgba(240, 240, 240, 0.5), -2px -2px 32px 0 rgba(240, 240, 240, 0.5);
}

.goTable, .goTable:visited {
  color: #f5f5f5;
  border-bottom: 1px solid;
  margin: 20px auto;
  display: inline-block;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  cursor: pointer;
}

@media all and (min-width: 800px) {
  .goTable, .goTable:visited {
    color: #0f0f0f;
  }
}

/* 為了比對結果 hover 做的 */
.wrap {
  padding-bottom: 200px;
  min-height: 3000px;
}

.pageTitle {
  margin: 0 auto;
  text-align: center;
  color: white;
  padding: 20px 10px;
  font-size: 24px;
  background: rgba(0, 0, 0, 0.42);
}

/* -----------------
/* CONFIG */
.configSection {
  max-width: 360px;
  margin: 40px 10px;
  line-height: 2;
  padding: 10px;
  background: white;
  box-shadow: 3px 3px 0 0 rgba(0, 0, 0, 0.12);
}

@media all and (min-width: 360px) {
  .configSection {
    margin: 40px auto;
  }
}

/* should be refine later */
.list {
  padding: 0.5rem 1rem;
  margin: 0.5rem 0.5rem 2rem 0.5rem;
  list-style-type: none;
}

.listItem {
  margin: 0 0 0.5rem 0;
  padding: 0;
}

.radioLabel {
  position: relative;
  margin: .5rem;
  font-family: Arial, sans-serif;
  line-height: 135%;
  cursor: pointer;
}

.radio {
  position: relative;
  margin: 0 1rem 0 0;
  cursor: pointer;
}

.radio:before {
  transition: all 0.4s cubic-bezier(0.45, 1.8, 0.5, 0.75);
  transform: scale(0, 0);
  content: "";
  position: absolute;
  top: 0;
  left: 0.125rem;
  z-index: 1;
  width: 0.75rem;
  height: 0.75rem;
  border-radius: 50%;
}

.radio:checked:before {
  background: #005060;
  transform: scale(1, 1);
}

.radio:after {
  content: "";
  position: absolute;
  top: -0.25rem;
  left: -0.125rem;
  width: 1rem;
  height: 1rem;
  background: #fff;
  border: 2px solid #005060;
  border-radius: 50%;
}

.button {
  margin-left: calc(50% - 55px);
  display: block;
  background: #005060;
  border: none;
  padding: 3px 30px;
  color: white;
}

/* -----------------
/* GAME */
/* billboard only in web version */
.billboardBlock {
  position: fixed;
  bottom: 200px;
  width: 1000px;
  left: calc(50% - 500px);
  z-index: 99;
}

.billboard {
  position: absolute;
  z-index: 10;
  display: inline-block;
}

.billboard.left {
  width: 340px;
  left: 10px;
}

.billboard.right {
  right: 10px;
  width: 340px;
  float: left;
  margin: 0;
}

.billboard.right .partyMenuItem:nth-child(5) {
  margin-left: 70px;
}

