  max-width: 800px;
  margin: 50px auto;
}

.figHeader {
  background: #f5f5f5;
  padding: 10px 0 20px 0;
}

.figDes {
  padding: 0 24px;
  text-align: left;
  font-size: 13px;
  line-height: 1.5;
  color: #323232;
}

.positionTitle {
  font-size: 18px;
  font-weight: normal;
  margin: 0;
  padding: 8px;
  width: auto;
}

@media all and (min-width: 1000px) {
  .positionTitle {
