@import '../common.scss';
.innerTag {
	width: 100%;
	margin: 0;
	padding: 0;
	text-align: center;
}
.innerTag li {
 	@include inline-middle;
	float: none;
	cursor: pointer;
}
.innerTag a, .innerTag a:visited {
	display: block;
	padding: 0.5em 1em;
	background: $lightGrey;
	color: $black;
	text-decoration: none;
}
.innerTag a:hover, .innerTag a.active {
	background: $black;
	color: $white;
}


