.wrap {
  text-align: left;
  position: relative;
  max-width: 600px;
  margin: 0 auto;
}

.controls {
  position: relative;
}

.sortingButtons {
  display: none;
}

@media screen and (min-width: 600px) {
  .controls {
    width: 600px;
  }
  .sortingButtons {
    display: inline-block;
    position: absolute;
    right: 0;
    top: 0;
  }
}

.sortingButton {
  display: block;
  width: auto;
  color: #555;
  background: #ddd;
  padding: 10px;
  margin: 0;
  cursor: pointer;
  display: inline-block;
}

.sortingButton:hover, .sortingButtonActive {
  background: #0f0f0f;
  color: #f5f5f5;
}

/*----control-----*/
.controlButton {
  display: block;
  width: auto;
  color: #555;
  background: #ddd;
  padding: 10px;
  margin: 0;
  cursor: pointer;
  display: inline-block;
}

.controlButton:hover, .controlButtonActive {
  background: #0f0f0f;
  color: #f5f5f5;
}

/*----data-----*/
.aRecordRow {
  display: block;
  padding: 20px;
  background: white;
  margin-top: 1px;
}

.positionCube {
  display: inline-block;
  vertical-align: middle;
  width: 20px;
  height: 20px;
  margin: 0 0.5em 0 0;
}

.date, .category {
  display: inline-block;
  vertical-align: middle;
  margin: 0 0.5em;
}

.avatar, .avatar:visited {
  position: absolute;
  display: block;
  overflow: hidden;
  text-align: center;
  text-decoration: none;
  width: 60px;
}

.avatarImg {
  overflow: hidden;
  width: 40px;
  height: 50px;
  margin: 0 auto;
}

.avatarName {
  font-size: 14px;
  line-height: 1.2;
  color: #0f0f0f;
  text-align: center;
}

.avatarName > .name {
  display: inline-block;
  border-bottom: 1px solid transparent;
}

.avatar:hover .avatarName > .name {
  border-bottom-color: #0f0f0f;
}

.detail {
  margin-left: 80px;
}

.content {
  margin-top: 10px;
  line-height: 1.5;
}

.evadingItem {
  margin: 20px;
  text-align: center;
  color: #555;
}

.evadingText {
  font-size: 20px;
  margin: 20px 0;
}

.evadingFAQ {
  margin: 10px 0;
}

