.innerTag {
  width: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
}

.innerTag li {
  display: inline-block;
  vertical-align: middle;
  float: none;
  cursor: pointer;
}

.innerTag a, .innerTag a:visited {
  display: block;
  padding: 0.5em 1em;
  background: #ddd;
  color: #0f0f0f;
  text-decoration: none;
}

.innerTag a:hover, .innerTag a.active {
  background: #0f0f0f;
  color: #f5f5f5;
}

.wrap {
  position: relative;
  text-align: center;
}

.innerWrap {
  padding: 20px 0 100px 0;
}

.bottomWrap {
  background: #DDD;
  padding: 20px 0;
}

.issueBlock, .issueBlock:visited {
  display: block;
  vertical-align: top;
  padding: 20px;
  color: #0f0f0f;
}

.issueBlock:hover {
  background: #ddd;
}

@media all and (min-width: 400px) {
  .innerWrap {
    max-width: 800px;
    margin: 0 auto;
  }
  .issueBlock, .issueBlock:visited {
    display: inline-block;
  }
}

.partyLink, .partyLink:visited {
  color: #0f0f0f;
}



