.wrap {
  position: relative;
  max-width: 720px;
  /* 3 castles */
  margin: 0 auto;
  text-align: center;
}

.wrap.embed {
  display: inline;
}

.wrap.showingOtherIssues {
  padding-top: 50px;
}

/* 手機上 safari 需要指定 block */
.coverItem, .coverItem:visited {
  position: relative;
  text-decoration: none;
  display: block;
  vertical-align: middle;
  width: 240px;
  margin: 0 auto;
  padding: 0;
  overflow: hidden;
  color: #0f0f0f;
  text-align: center;
  cursor: pointer;
  transition: color 100ms ease-in-out;
}

/* 用在 embed 需要白色背景 */
.coverItem.embed {
  margin: 10px;
  padding: 20px 10px;
  vertical-align: top;
}

@media all and (min-width: 480px) {
  .coverItem, .coverItem:visited {
    display: inline-block;
    margin: 0;
  }
}

@media all and (max-width: 239px) {
  .coverItem, .coverItem:visited {
    width: 96%;
    margin: 0 2%;
  }
}

.coverItem:hover .coverImg {
  -webkit-filter: drop-shadow(0 0 15px rgba(255, 255, 0, 0.75));
  -ms-filter: drop-shadow(0 0 15px rgba(255, 255, 0, 0.75));
  filter: drop-shadow(0 0 15px rgba(255, 255, 0, 0.75));
}

.coverImg {
  display: block;
  width: 100%;
}

.coverTitleImg {
  display: block;
  width: 100%;
}

.coverTitleBlock {
  text-align: center;
  line-height: 24px;
}

.coverTitle {
  color: #005060;
  font-size: 24px;
  margin: 0 2px;
  font-weight: bold;
  display: inline-block;
}

.completedCoverTitle {
  color: #0f0f0f;
}

.coverQuestion {
  line-height: 32px;
}

.missionStatusBlock {
  display: block;
}

.missionStatusBlock > * {
  display: inline-block;
  vertical-align: middle;
}

.missionStatusText {
  color: #005060;
  font-weight: bold;
}

.star {
  transition: transform 0.5s ease-in-out;
}

.missionAccomplishBadge {
  position: absolute;
  top: 0;
  left: 20px;
}

.coverItem:hover .star {
  -webkit-filter: drop-shadow(0 0 3px gold);
  -ms-filter: drop-shadow(0 0 3px gold);
  filter: drop-shadow(0 0 3px gold);
  transform: rotate(180deg);
}

.comingText {
  text-align: center;
  font-size: 20px;
  color: #aaa;
}

.coverItem.embed .comingText {
  color: #302F2F;
  margin-bottom: 10px;
}



