@import '../../common.scss';
.wrap {
	position: relative;
}
.bunItem, .bunItem:visited {
	display: inline-block;
	margin: 3px;
    box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
    background: white;
    padding: 30px 8px;
    width: 120px;
    height: 100px;
    vertical-align: top;
    cursor: pointer;
    text-decoration: none;
    color: $black;
}
.bunItem:hover {
	box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.22);
}
.partyNameWrap {
    margin-top: 4px; 
}
.partyName {
    border-bottom: 1px solid transparent;
    display: inline;
    padding-bottom: 1px;
}
.bunItem:hover .partyName {
    border-bottom: 1px solid $black;
}




