.symbol {
  display: inline-block;
  width: 32px;
  height: 32px;
  margin: 0;
  padding: 0;
}

a.ia {
  line-height: 1.25;
  text-decoration: none;
  border-bottom: 1px solid transparent;
}

a.ia.bright {
  color: #005060;
}

a.ia.bright:hover {
  border-bottom-color: #005060;
}

a.ia.black {
  color: #0f0f0f;
}

a.ia.black:hover {
  border-bottom-color: #0f0f0f;
}

a.ia.dim {
  color: #aaa;
}

a.ia.dim:hover {
  border-bottom-color: #aaa;
}

a.ia.inverted {
  color: #f5f5f5;
}

a.ia.inverted:hover {
  border-bottom-color: #f5f5f5;
}

a.ia.big {
  border-bottom-width: 3px;
}

a.ia.line {
  border-bottom: 1px solid #0f0f0f;
  padding-bottom: 1px;
  margin: 1px;
  display: inline-block;
}

