.wrap {
  position: relative;
  padding: 10vh 0;
  text-align: center;
  min-height: 50vh;
}

.input {
  padding: 10px;
  width: 300px;
  box-shadow: none;
  border: 1px solid #ccc;
  padding: 10px;
  outline: none;
}

.input:focus {
  border: 1px solid #444;
}

.inputHint {
  font-size: 14px;
  margin: 4px;
  color: #505050;
}

.noResult {
  margin-top: 10px;
}

@keyframes blink {
  50% {
    opacity: 1;
  }
}

@keyframes bulge {
  50% {
    transform: scale(1.05);
  }
}

.indicator {
  width: auto;
  border-radius: 50px;
  padding: 20px;
  display: table;
  margin: 0 auto;
  position: relative;
  animation: bulge 2s infinite ease-out;
  -moz-animation: bulge 2s infinite ease-out;
  -webkit-animation: bulge 2s infinite ease-out;
}

.indicator:after {
  height: 10px;
  width: 10px;
  left: -10px;
  bottom: -10px;
}

.indicator span {
  height: 15px;
  width: 15px;
  float: left;
  margin: 0 1px;
  background-color: #9E9EA1;
  display: block;
  border-radius: 50%;
  opacity: 0.4;
}

.indicator span:nth-of-type(1) {
  animation: blink 1s infinite 0.3333s;
}

.indicator span:nth-of-type(2) {
  animation: blink 1s infinite 0.6666s;
}

.indicator span:nth-of-type(3) {
  animation: blink 1s infinite 0.9999s;
}

/* peopleLink */
.resultSection {
  max-width: 720px;
  margin: 0 auto;
  text-align: center;
  padding-top: 30px;
}

.resultCount {
  font-size: 18px;
  margin-bottom: 20px;
}

.candidate, .candidate:visited {
  display: inline-block;
  background: white;
  color: #0f0f0f;
  margin: 4px;
  padding: 10px 0;
  text-align: center;
  text-decoration: none;
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.08);
  width: 120px;
  height: 160px;
  overflow: hidden;
  position: relative;
}

.candidate:hover {
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
}

.photo {
  width: 60px;
  height: 60px;
  margin: 0 auto 4px auto;
}

.partyItem {
  display: inline-block;
  vertical-align: top;
  padding-right: 2px;
}

.partyText {
  font-size: 14px;
  display: inline-block;
  margin-right: 10px;
}

.nameItem {
  margin: 2px;
  font-weight: 600;
}

.districtInfo {
  font-size: 14px;
}



