.wrap {
  position: relative;
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}

.prevNextWrap {
  width: 100%;
  background: #ddd;
  font-size: 0;
}

.prev, .next {
  display: inline-block;
  width: 50%;
  cursor: pointer;
}

.prev_next {
  display: block;
  height: 30px;
  margin: 7px auto;
}

.next {
  transform: rotate(180deg);
}

@media all and (min-width: 600px) {
  .prevNextWrap {
    position: absolute;
    top: 0;
    background: none;
  }
  .prev, .next {
    width: 50%;
    height: 0;
    padding-bottom: 42.5%;
    opacity: 0.15;
  }
  .prev:hover, .next:hover {
    opacity: 1.0;
  }
  .prev_next {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    height: 43px;
    margin: 0;
  }
}

.inactiveSlideImg {
  position: absolute;
  width: 100%;
  opacity: 0;
  top: 0;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

.activeSlideImg {
  width: 100%;
  opacity: 1;
}

.slideBlock {
  position: relative;
  top: 0;
  width: 100%;
  display: inline-block;
}

.slideBlock img {
  display: block;
}

.nextPageButton {
  cursor: pointer;
  display: inline-block;
  padding: 10px 20px;
  background: #523F3F;
  border-radius: 3px;
  color: white;
  margin: 10px;
  box-shadow: 0px 4px 0 0 rgba(82, 63, 63, 0.53);
}

.nextPageButton:hover {
  box-shadow: 0px 4px 0 0 transparent;
  margin-top: 14px;
}

.pageWrap {
  position: relative;
}

.page {
  display: inline-block;
  cursor: pointer;
  display: inline-block;
  margin: 8px 4px;
  padding: 0;
  width: 16px;
  height: 16px;
  border-radius: 100%;
  background: #aaa;
}

.page:hover, .activePage {
  color: white;
  background: #0f0f0f;
}

.menuBlock {
  display: inline-block;
  vertical-align: top;
}

.menuItem {
  text-align: left;
  vertical-align: top;
  padding: 10px;
  width: 200px;
  line-height: 1.4;
  background: white;
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
  margin-bottom: 2px;
  margin-right: 8px;
  cursor: pointer;
}

.menuItem.activeMenuItem {
  color: white;
  background: #523F3F;
}



