.wrap {
  padding: 20px 10px;
  min-height: 100vh;
  background: white;
}

.innerWrap {
  margin: 0 auto;
  max-width: 800px;
  position: relative;
  padding: 20px;
  margin-bottom: 200px;
}

.subscribeContent {
  margin: -80px 10% 60px 10%;
}

@media all and (min-width: 540px) and (max-width: 800px) {
  .subscribeContent {
    position: absolute;
    margin: 0 8%;
    top: 45%;
  }
}

@media all and (min-width: 800px) {
  .innerWrap {
    margin-top: 10vh;
    min-height: 0;
    padding: 0;
  }
  .subscribeContent {
    position: absolute;
    left: 7%;
    margin: 0 10%;
    bottom: 40%;
  }
}

.postcardImg {
  width: 100%;
  border: 1px solid #EAEAEA;
  box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.12);
}

.titleMobile {
  font-size: 20px;
  padding: 20px 0;
  font-weight: bolder;
}

.titleWeb {
  display: none;
}

@media all and (min-width: 800px) {
  .titleMobile {
    display: none;
  }
  .titleWeb {
    font-size: 20px;
    padding: 20px 0;
  }
}

.emailLabel {
  display: none;
}

.red {
  color: #D65A5A;
}

.showAlert {
  display: inline-block;
  color: #D65A5A;
}

.hideAlert {
  display: none;
}

.emailInput {
  display: inline-block;
  height: 40px;
  padding: 5px 10px;
  background: rgba(245, 245, 245, 0.75);
  color: #0f0f0f;
  line-height: 1;
  border: none;
  border-bottom: 1px solid gray;
  width: 100%;
}

.emailInput:focus {
  outline: none;
  background: #f5f5f5;
  border: none;
  border-bottom: 1px solid black;
}

.submitMobile {
  display: block;
  height: 40px;
  padding: 0 8px;
  border: none;
  color: white;
  background: gray;
  margin: 20px auto;
}

.submitWeb {
  display: none;
}

@media all and (min-width: 540px) and (max-width: 800px) {
  .emailInput {
    width: 290px;
  }
  .submitMobile {
    display: none;
  }
  input.submitWeb {
    display: inline-block;
    margin: 0 10px;
    height: 40px;
    padding: 0 8px;
    border: none;
    color: white;
    background: gray;
  }
}

@media all and (min-width: 800px) {
  .emailInput {
    width: 390px;
  }
  .submitMobile {
    display: none;
  }
  input.submitWeb {
    display: inline-block;
    margin: 0 10px;
    height: 40px;
    padding: 0 8px;
    border: none;
    color: white;
    background: gray;
  }
}

.submitMobile:hover, .submitWeb:hover {
  background: black;
}

.lawInfo {
  line-height: 1.5;
  font-size: 14px;
  color: gray;
  padding: 10px;
}

.lawInfo, .lawInfoTitle, .lawInfoContent {
  margin: 12px 0;
}

.linkItem {
  display: inline-block;
  margin: 10px;
  font-size: 16px;
}

.smallIcon {
  font-size: 16px;
  margin: 4px;
}



