.articleWrap {
  position: relative;
  max-width: 600px;
  margin: 0 0 150px;
}

.article {
  text-align: left;
  background: #fff;
  padding: 20px;
  margin-top: 100px;
  background: #fff;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.15);
}

@media all and (min-width: 600px) {
  .articleWrap {
    margin: 0 auto 150px auto;
  }
  .article {
    padding: 40px;
    box-shadow: 4px 4px 8px 0 rgba(0, 0, 0, 0.15);
  }
}

.img {
  position: absolute;
  width: 200px;
  top: -160px;
  left: -10px;
  float: left;
}

.bigTitle {
  font-size: 24px;
  font-weight: bold;
  color: #005060;
  line-height: 1.5;
  text-align: center;
}

.bigSubtitle {
  font-size: 20px;
  color: #555;
  line-height: 1.5;
  text-align: center;
}

.article:after {
  content: '';
  position: absolute;
  top: -32px;
  left: 144px;
  border-bottom: 32px solid #fff;
  border-left: 0 solid transparent;
  border-right: 32px solid transparent;
}

