@import '../../common.scss';
.QAItem {
	position: relative;
	display: none;
	margin: 30px 10px;
} 
@media screen and (min-width: 400px){
	.QAItem {
		width: 420px;
		margin: 0 auto;
		padding: 30px;
	    position: relative;
	    display: none;
    } 
}
.QAItem.isActive {
	display: block;
}
.questionContent {
	background: rgb(247,247,247);
    padding: 20px 10px;
    box-shadow: 3px 3px rgba(0,0,0,0.12);
}
.questionTitle {
	font-size: 20px;
	font-weight: 800;
	padding: 10px;
}
.questionDescription {
	padding: 10px;
	line-height: 1.6;
	
}
.optionSection{
	margin: 0 auto;
	width: 100%;
	line-height: 1.6;
	text-align: center;
	padding: 50px 0 30px 0;
}
.optionItem {
	margin: 5px;
	padding: 4px 10px;
	cursor: pointer;
	display: inline-block;
	border: 1px solid;
}
@media all and (min-width: 400px){
	.optionItem {
		padding: 4px 20px;
	}
}
.optionItem.aye {
	border-color: $ayeColor;
	color: $ayeColor;
	background: none;
}
.optionItem.nay {
	border-color: $nayColor;
	color: $nayColor;
	background: none;
}
.optionItem.unknown {
	border-color: $unknownColor;
	color: $unknownColor;
	background: none;
}
.optionItem.none {
	border-color: $noneColor;
	color: $noneColor;
	background: none;
}
.optionItem.aye:hover, .optionItem.isChoosed.aye {
	color: white;
	background: $ayeColor;
}
.optionItem.nay:hover, .optionItem.isChoosed.nay {
	color: white;
	background: $nayColor;
}
.optionItem.unknown:hover, .optionItem.isChoosed.unknown {
	color: white;
	background: $nayColor;
}
.optionItem.none:hover, .optionItem.isChoosed.none {
	color: white;
	background: $noneColor;
}
.optionItem.isChoosed {
	transition: background 1s;
}






