.wrap {
  display: none;
}

@media all and (min-width: 800px) {
  .wrap {
    display: block;
    margin: 0;
    padding: 0;
    position: relative;
    font-family: sans-serif;
  }
}

.shelf {
  display: none;
}

.backgroundRect {
  fill: none;
}

g {
  cursor: pointer;
}

.bigmap {
  display: none;
}

@media all and (min-width: 800px) {
  .shelf {
    display: block;
    margin: 0 auto;
  }
  .bigmap {
    display: block;
    position: absolute;
    top: 50px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
    transform: translateX(-50%);
  }
}

/* 大台灣地圖上的縣市名稱，滑鼠滑過去才出現 */
.cityName {
  display: none;
}

.cityName.active {
  display: block;
}

/* 詳細選區 */
@media all and (min-width: 800px) {
  .cityMap {
    z-index: 90;
  }
  .cityMap.active {
    background: rgba(0, 0, 0, 0.75);
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    height: 100vh;
    padding-top: 10vh;
  }
}

.city {
  fill: white;
  margin: 0 auto;
  background: #9ce5ff;
  padding: 30px 0;
  font-size: 22px;
  font-weight: 600;
}

.city.active:after {
  content: '';
  display: block;
  width: 30px;
  height: 1px;
  margin: 30px auto;
  background: white;
}

@media all and (min-width: 800px) {
  .city {
    display: none;
  }
  .city > .title {
    display: none;
  }
  .city.active {
    display: block;
    background: none;
    margin: 0;
    padding: 0;
    font-size: 16px;
    font-weight: 100;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -40%);
    -ms-transform: translate(-50%, -40%);
    transform: translate(-50%, -40%);
  }
  .city.active:after {
    width: 0;
  }
}

.map {
  margin-top: 20px;
  overflow: scroll;
}

@media all and (min-width: 800px) {
  .map {
    margin: 0;
  }
}

/* 下方子區域的地圖 */
.cityPath {
  fill: white;
}

.cityPath:hover, .cityPath.active {
  fill: #00ffb0;
}

/* 處理手機部分某些縣市需要 transform */
.citymapNTC {
  -webkit-transform: translate3d(-360px, 0, 0) translateX(50vw);
  -ms-transform: translate3d(-360px, 0, 0) translateX(50vw);
  transform: translate3d(-360px, 0, 0) translateX(50vw);
}

.citymapKHH {
  -webkit-transform: translate3d(-140px, 0, 0) translateX(50vw);
  -ms-transform: translate3d(-140px, 0, 0) translateX(50vw);
  transform: translate3d(-140px, 0, 0) translateX(50vw);
}

@media all and (min-width: 1015px) {
  .citymapNTC, .citymapKHH {
    -webkit-transform: none;
    -ms-transform: none;
    transform: none;
  }
}

.close.active {
  width: 64px;
  height: 64px;
  margin: 64px auto;
  position: relative;
  cursor: pointer;
  background: yellow;
  z-index: 1;
  position: fixed;
  top: calc(-10vh - 2px);
  left: 0;
  right: 0;
}

.close.active.inactive {
  background: white;
}

.close.active:hover {
  background: #ff0055;
}

.close.active:before, .close.active:after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background: black;
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.close.active:after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

@media all and (min-width: 800px) {
  .close.active {
    position: absolute;
    top: -10px;
  }
}

/* 詳細選區: xx市, xx區 */
.info.active {
  display: block;
}

.name, .prompt, .arrow {
  display: inline-block;
}

.prompt {
  background: yellow;
}

@media all and (min-width: 800px) {
  .info > .go > .prompt, .info > .go > .arrow {
    display: none;
  }
}

.info {
  display: none;
  box-sizing: border-box;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  margin: 0;
  padding: 0;
  background: white;
  border-top: 2px solid black;
  color: black;
}

.info > .close {
  position: absolute;
  top: 0;
  right: 0;
  width: 48px;
  height: 48px;
  margin: 0;
  background: #ff0055;
  cursor: pointer;
}

.info > .close:before, .info > .close:after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background: black;
  border-radius: 1px;
  position: absolute;
  top: calc(50% - 1px);
  left: 0;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.info > .close:after {
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.info > .go {
  display: block;
  margin: 0;
  padding: 15px 30px 15px 15px;
  font-size: 18px;
  line-height: 1;
  background: #00ffb0;
  color: black;
  text-decoration: none;
}

@media all and (min-width: 800px) {
  .info > .go {
    padding: 15px;
  }
}

.info > .go > * {
  display: inline-block;
  vertical-align: middle;
  margin: 0 4px 0 0;
  padding: 0;
}

.info > .go > .prompt {
  font-size: 0.85em;
  border-top: 1px solid transparent;
  border-bottom: 1px solid black;
}

.info > .go > .arrow {
  width: 33.94145px;
  height: 2px;
  background: black;
  border-radius: 1px;
  position: relative;
}

.info > .go > .arrow:before, .info > .go > .arrow:after {
  content: '';
  display: block;
  width: 24px;
  height: 2px;
  background: black;
  border-radius: 1px;
  position: absolute;
  left: calc(50% - 3px);
}

.info > .go > .arrow:before {
  top: -8px;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
  transform: rotate(45deg);
}

.info > .go > .arrow:after {
  top: 8px;
  -webkit-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.info > .detail {
  margin: 7px 14px 14px 14px;
  font-size: 14px;
}

.info .subdistrict {
  display: inline-block;
  vertical-align: top;
  margin-right: 1em;
}

.info .boroughs {
  font-size: 11px;
  margin-top: 5px;
}

.info .boroughs .borough {
  display: inline-block;
  vertical-align: top;
  margin-right: 1em;
}

.info > .go {
  background: none;
}

.info > .go:hover {
  background: #00ffb0;
}

.info > .close {
  background: none;
}

.info > .close:hover {
  background: #ff0055;
}



