.wrap {
  display: inline-block;
  vertical-align: top;
}

.wrap > * {
  display: block;
  margin: 0 auto;
}

.title {
  padding: 4px 4px 0;
  width: 124px;
  font-size: 19px;
  background: #000;
  color: #fff;
}

.countWrap {
  display: inline-block;
  border: 2px solid #000;
  font-size: 16px;
  line-height: 1;
}

.countTotal {
  display: inline-block;
  width: 120px;
  padding: 0 0 4px;
  background: #000;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
}

.countItem {
  display: inline-block;
  width: 40px;
  padding: 4px 0;
}

.cubesOuterWrap {
  margin-top: 20px;
}

/* 一個一個表態 */
.positionWrap {
  position: relative;
  float: left;
}

.positionCube {
  width: 20px;
  height: 20px;
  float: left;
}

/* hover 上的時候顯示的文字 */
.activeCube {
  width: 100%;
  background: white;
  position: absolute;
  text-align: left;
  padding: 10px;
  font-size: 14px;
  line-height: 1.4;
  bottom: -100px;
  height: 90px;
  width: 340px;
  border: 1px solid;
  overflow: hidden;
  z-index: 1;
  box-shadow: 2px 2px 4px 0 rgba(0, 0, 0, 0.12);
}



