.wrap {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  padding-top: 50px;
  text-align: center;
}

.district {
  /* 臺北市 */
  margin-bottom: 80px;
}

.subDistrict {
  /* 臺北市 xx 選區 */
  margin-bottom: 40px;
}

.subDistrictText, .subDistrictText:visited {
  color: #646464;
  font-size: 18px;
  margin: 5px 0;
  text-decoration: none;
}

.subDistrictText:hover {
  color: #0f0f0f;
  text-decoration: underline;
}

.candidate, .candidate:visited {
  display: inline-block;
  background: white;
  color: #0f0f0f;
  margin: 4px;
  padding: 10px 0;
  text-align: center;
  text-decoration: none;
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.08);
  width: 120px;
  height: 160px;
  overflow: hidden;
  position: relative;
}

.candidate:hover {
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
}

.shadow {
  background: rgba(0, 0, 0, 0.5);
  width: 120px;
  height: 160px;
  position: absolute;
  top: 0;
}

.shadow:after {
  content: '尚未回覆';
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  color: #272727;
  font-weight: 800;
  text-align: center;
  width: 100%;
}

.shadow.hasReply {
  display: none;
}

.shadow.hasReply:after {
  content: '';
}

.photo {
  width: 110px;
  height: 110px;
  margin: 0 auto;
}

.partyItem {
  display: inline-block;
  vertical-align: top;
  padding-top: 8px;
}

.nameItem {
  display: inline-block;
  margin: 10px 4px;
  max-width: 80px;
}

/* ----------------- */
.actionPanel {
  margin-top: 40px;
}

.buttonSet {
  text-align: center;
}

.button {
  cursor: pointer;
  display: inline-block;
  border: 1px solid gray;
  padding: 4px;
  margin: 4px;
}

.button.active {
  cursor: pointer;
  display: inline-block;
  border: 1px solid gray;
  background: lightgray;
}

.button:hover {
  background: lightgray;
}



