.actionText {
  margin: 0 auto 20px auto;
  color: white;
  padding-left: 18px;
  font-size: 14px;
}

@media all and (min-width: 800px) {
  .actionText {
    color: #0f0f0f;
  }
}

.actions {
  line-height: 1.25;
}

.actionButtons {
  font-size: 22px;
  position: relative;
  text-align: center;
  max-width: 420px;
  margin: 40px auto 30px auto;
}

.actionButton {
  display: inline;
  font-size: 24px;
  position: relative;
  margin: 0 4px;
  padding: 8px 20px;
  height: 40px;
  background: #005060;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.25);
  color: #f5f5f5;
  border-radius: 0;
  cursor: pointer;
}

.actionButton:focus {
  outline: none;
}

.actionButton:hover {
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.5);
}

.goMatch, .goMatch:visited {
  cursor: pointer;
  display: inline-block;
  margin: 0 auto;
  background: #005060;
  text-decoration: none;
  color: white;
  padding: 10px 20px;
  box-shadow: 0 0 12px 0 rgba(240, 240, 240, 0.3), 0 0 12px 0 rgba(240, 240, 240, 0.3);
}

.goMatch:hover {
  box-shadow: 2px 2px 32px 0 rgba(240, 240, 240, 0.5), -2px -2px 32px 0 rgba(240, 240, 240, 0.5);
}

.goTable, .goTable:visited {
  color: #f5f5f5;
  border-bottom: 1px solid;
  margin: 20px auto;
  display: inline-block;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  cursor: pointer;
}

@media all and (min-width: 800px) {
  .goTable, .goTable:visited {
    color: #0f0f0f;
  }
}

.wrap {
  min-height: 60vw;
  background-image: url("../images/parties-mobile.jpg");
  padding-top: 50px;
}

@media all and (min-width: 800px) {
  .wrap {
    background: none;
  }
  .bgHolder {
    left: 0px;
    right: 0px;
    bottom: 0px;
    top: 0px;
    position: fixed;
    background-image: url("../images/parties-web.jpg");
    background-size: cover;
    transform: translateZ(0);
    pointer-events: none;
    z-index: -1;
  }
}

/* 說明 */
.initialWrap {
  padding-top: 40px;
  min-height: 600px;
}

.story {
  max-width: 400px;
  margin: 0px auto 40px auto;
  padding: 20px;
  line-height: 1.6;
  background: rgba(0, 0, 0, 0.8);
  color: white;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.22);
  text-align: center;
}

.actions {
  text-align: center;
}



