.wrap {
  position: relative;
}

/* ---------------- */
.actionPanel {
  margin: 10px 0;
}

.actionSet {
  margin-top: 4px;
  font-size: 14px;
}

.toggleButton {
  cursor: pointer;
  display: inline-block;
  margin: 4px;
  padding: 3px 10px;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.08);
  border: 1px solid #AD171F;
  color: #AD171F;
}

.toggleButton.active {
  background: #AD171F;
  color: white;
}

.toggleButton:hover {
  background: #AD171F;
  color: white;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.28);
}

/* --- */
.toggleButtonB {
  cursor: pointer;
  display: inline-block;
  margin: 4px;
  padding: 3px 10px 2px 10px;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.08);
  border: 1px solid #323232;
  color: #323232;
}

.toggleButtonB.active {
  background: #323232;
  color: white;
}

.toggleButtonB:hover {
  background: #323232;
  color: white;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.28);
}

/* ---------------- 圖例 */
.legend {
  opacity: 0;
  transition: all 400ms ease-in;
  will-change: opacity;
}

.legend.active {
  opacity: 1;
  text-align: center;
  margin: 30px 0 0 0;
}

.legend.active > img {
  display: inline-block;
  vertical-align: top;
}

/* ---------------- */
.card {
  vertical-align: top;
  margin: 10px;
  padding: 10px 10px 20px 10px;
  background: white;
  box-shadow: 2px 2px 12px 0 rgba(0, 0, 0, 0.12);
  position: relative;
  vertical-align: top;
  cursor: pointer;
  min-height: 230px;
}

@media all and (min-width: 380px) {
  .card {
    min-width: 380px;
  }
}

@media all and (min-width: 480px) {
  .card {
    display: inline-block;
  }
}

.card:hover {
  box-shadow: 2px 2px 12px 0 rgba(0, 0, 0, 0.22);
}

.cardLink, .cardLink:visited {
  color: #0f0f0f;
  text-decoration: none;
  display: inline-block;
}

.peoplePhoto {
  width: 70px;
  height: 70px;
  background: white;
  margin: 0 auto;
  border-radius: 50%;
  display: inline-block;
}

.basicInfo {
  display: inline-block;
  margin: 0 10px;
  padding-top: 8px;
  vertical-align: top;
}

.metaInfo {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 40px;
}

.currentInfo {
  font-size: 12px;
  background: #AD171F;
  color: white;
  padding: 4px;
}

.currentInfo.wide {
  width: 60px;
  position: absolute;
  left: -10px;
}

@media all and (min-width: 320px) {
  .currentInfo.wide {
    width: 70px;
    position: relative;
    left: auto;
  }
}

.noContactInfo {
  color: gray;
  padding: 1px;
  font-size: 12px;
  width: 54px;
  padding: 2px 1px 0 1px;
  border: 1px solid;
}

.peopleNumber {
  display: inline-block;
  font-size: 22px;
  margin: 0 4px;
  vertical-align: top;
  letter-spacing: -1px;
  font-weight: bold;
}

.peopleName {
  font-size: 16px;
  font-weight: 600;
  padding: 0;
  border-bottom: 2px solid;
  display: inline-block;
  margin: 2px 0 4px;
  vertical-align: top;
}

.partyItem {
  left: 8px;
  top: 16px;
}

/* ---------------- */
.position {
  text-align: center;
  display: inline-block;
  height: 45px;
}

@media all and (min-width: 450px) {
  .position {
    padding: 10px;
  }
}

.issueName {
  width: 40px;
  height: 60px;
  font-size: 14px;
  vertical-align: top;
  display: inline-block;
  font-weight: 600;
}

.record {
  vertical-align: middle;
  font-size: 0;
  height: 40px;
  position: relative;
}

.recordSquare {
  margin-bottom: 5px;
  display: inline-block;
}

.recordSquare.level1 {
  width: 10px;
  height: 10px;
}

.recordSquare.level2 {
  width: 20px;
  height: 20px;
}

.recordSquare.level3 {
  width: 40px;
  height: 40px;
}

.recordSquare.empty {
  width: 10px;
  height: 10px;
  border: 1px solid #ddd;
  margin-top: 5px;
}

.recordDetail {
  display: none;
}

@media all and (min-width: 800px) {
  .record:hover .recordDetail {
    z-index: 1;
    display: block;
    position: absolute;
    font-size: 14px;
    width: 120px;
    line-height: 1.4;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    text-align: left;
    padding: 10px;
    top: -90px;
    left: -39px;
    box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
  }
  .record.empty:hover .recordDetail {
    display: block;
    top: -44px;
    text-align: center;
  }
  .record:hover .recordDetail:after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 53px;
    border-top: 10px solid #25282A;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
  }
}

.promise {
  position: relative;
}

.promiseImg {
  width: 20px;
  display: inline-block;
  vertical-align: middle;
}

@media all and (min-width: 800px) {
  .promiseImg {
    margin: 10px 0;
  }
}

.promiseDetail {
  display: none;
}

@media all and (min-width: 800px) {
  .promise:hover .promiseDetail {
    z-index: 1;
    display: block;
    position: absolute;
    font-size: 14px;
    width: 100px;
    text-align: center;
    line-height: 1.4;
    background: rgba(0, 0, 0, 0.8);
    color: white;
    padding: 7px;
    top: -54px;
    left: -16px;
    box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
  }
  .promise:hover .promiseDetail.none {
    top: -34px;
    width: 70px;
  }
  .promise:hover .promiseDetail:after {
    content: '';
    position: absolute;
    bottom: -10px;
    left: 30px;
    border-top: 10px solid #25282A;
    border-left: 6px solid transparent;
    border-right: 6px solid transparent;
  }
}

/* ---------------- */
.billSection {
  display: block;
  text-align: center;
  line-height: 1.3;
}

.billSection.noReply {
  padding-top: 30px;
  text-align: center;
  min-height: 80px;
}

.billList {
  display: inline-block;
  text-align: left;
  width: 100%;
  font-size: 16px;
  vertical-align: top;
  margin: 10px;
}



