.title {
  font-size: 20px;
  padding: 20px 0;
}

.emailLabel {
  display: none;
}

.red {
  color: #D65A5A;
}

.showAlert {
  display: inline-block;
  color: #D65A5A;
}

.hideAlert {
  display: none;
}

.emailInput {
  display: inline-block;
  height: 40px;
  border: none;
  padding: 5px 10px;
  background: rgba(245, 245, 245, 0.75);
  color: #0f0f0f;
  line-height: 1;
}

.emailInput:focus {
  outline: none;
  background: #f5f5f5;
}

.submit {
  display: block;
  height: 40px;
  margin-top: 10px;
  padding: 0 8px;
  border: none;
  color: #0f0f0f;
  background: rgba(245, 245, 245, 0.75);
}

.submit:hover {
  background: #f5f5f5;
}

@media all and (min-width: 480px) {
  .submit {
    display: inline-block;
    margin: 0 10px;
  }
}

.lawInfo {
  line-height: 1.5;
  font-size: 12px;
}

.bottomInfo {
  display: none;
  line-height: 1.5;
  font-size: 12px;
}

.lawInfo, .lawInfoTitle, .lawInfoContent, .bottomInfo {
  margin: 12px 0;
}

.lawInfo, .bottomInfo {
  opacity: 0.5;
}

@media all and (min-width: 480px) {
  .lawInfo, .bottomInfo {
    width: 50%;
  }
}

.linkItem {
  display: inline-block;
  margin: 10px;
  font-size: 16px;
}

.hiddenLinks {
  max-height: 0px;
  overflow: hidden;
}

.smallIcon {
  margin-top: 2px;
  vertical-align: top;
  width: 20px;
}



