.actionText {
  margin: 0 auto 20px auto;
  color: white;
  padding-left: 18px;
  font-size: 14px;
}

@media all and (min-width: 800px) {
  .actionText {
    color: #0f0f0f;
  }
}

.actions {
  line-height: 1.25;
}

.actionButtons {
  font-size: 22px;
  position: relative;
  text-align: center;
  max-width: 420px;
  margin: 40px auto 30px auto;
}

.actionButton {
  display: inline;
  font-size: 24px;
  position: relative;
  margin: 0 4px;
  padding: 8px 20px;
  height: 40px;
  background: #005060;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.25);
  color: #f5f5f5;
  border-radius: 0;
  cursor: pointer;
}

.actionButton:focus {
  outline: none;
}

.actionButton:hover {
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.5);
}

.goMatch, .goMatch:visited {
  cursor: pointer;
  display: inline-block;
  margin: 0 auto;
  background: #005060;
  text-decoration: none;
  color: white;
  padding: 10px 20px;
  box-shadow: 0 0 12px 0 rgba(240, 240, 240, 0.3), 0 0 12px 0 rgba(240, 240, 240, 0.3);
}

.goMatch:hover {
  box-shadow: 2px 2px 32px 0 rgba(240, 240, 240, 0.5), -2px -2px 32px 0 rgba(240, 240, 240, 0.5);
}

.goTable, .goTable:visited {
  color: #f5f5f5;
  border-bottom: 1px solid;
  margin: 20px auto;
  display: inline-block;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  cursor: pointer;
}

@media all and (min-width: 800px) {
  .goTable, .goTable:visited {
    color: #0f0f0f;
  }
}

.wrap {
  position: relative;
  min-height: 500px;
}

.innerWrap {
  text-align: center;
}

.figHeader {
  min-height: 80px;
  max-width: 800px;
  margin: 50px auto 0 auto;
}

.issueBlock {
  border: 2px solid;
  margin: 0 5px;
  font-size: 20px;
  display: inline-block;
}

@media all and (min-width: 800px) {
  .issueBlock {
    float: left;
    margin-top: 10px;
  }
  .issueController {
    float: right;
  }
}

.issueTitle {
  background: black;
  color: white;
  padding: 10px;
}

.issueStatement {
  background: white;
  padding: 10px;
}

@media all and (min-width: 440px) {
  .issueTitle {
    display: inline-block;
  }
  .issueStatement {
    display: inline-block;
  }
}

.records {
  margin: 60px auto;
  max-width: 1000px;
  position: relative;
  text-align: center;
  padding-bottom: 100px;
}

@media all and (min-width: 800px) {
  .mobilePadOnly {
    display: none;
  }
}

.collaboratorInfo {
  text-align: center;
  margin: 0 -9%;
  background: #d3d3d3;
}

.link, .link:visited {
  color: #b88234;
  text-decoration: none;
  display: inline-block;
  padding: 10px;
}

.link:hover {
  color: #b88234;
  text-decoration: underline;
}

.moreInfo {
  background: white;
  line-height: 2;
  text-align: left;
  padding-bottom: 50px;
}

.moreInfoInner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 10px;
  font-size: 22px;
  color: #353736;
}

.linkButton, .linkButton:visited {
  color: #D6A051;
  border: 2px solid #D6A051;
  padding: 0px 10px;
  font-size: 18px;
  margin: 0 10px 0px 10px;
  display: inline-block;
  text-decoration: none;
}

.linkButton:hover {
  background: #D6A051;
  color: white;
}

.comingButton {
  color: #D6A051;
  border: 2px dotted #D6A051;
  padding: 0px 10px;
  font-size: 18px;
  margin: 0 10px 0px 10px;
  display: inline-block;
  text-decoration: none;
}

.backStage {
  cursor: pointer;
  text-align: left;
  max-width: 480px;
  margin: 0 auto 4px auto;
  color: #6D6868;
  padding-left: 4px;
  min-height: 30px;
  display: none;
}

.backStage:hover {
  text-decoration: underline;
}

.mobileHint {
  position: relative;
}

@media all and (min-width: 800px) {
  .mobileHint {
    display: none;
  }
}

.keyboardHint {
  display: none;
}

@media all and (min-width: 800px) {
  .keyboardHint {
    display: block;
    margin: 20px 10px 100px 10px;
    color: #505050;
  }
}

.skipInteractive {
  margin: 20px;
  cursor: pointer;
}

@media all and (min-width: 800px) {
  .skipInteractive {
    display: inline-block;
    margin: 0;
  }
}

/* 協力 NGO*/
.collaboratorInfo {
  text-align: center;
  padding: 10px 0;
  background: #ddd;
  border-bottom: 1px solid silver;
}

.completeMessage {
  text-align: left;
  max-width: 600px;
  margin: 50px auto 0;
  position: relative;
  font-size: 18px;
  text-align: center;
  line-height: 1.4;
}



