.appbar {
  position: absolute;
  top: 0;
  left: 0;
  background: #f5f5f5;
  padding: 0px;
  height: 76px;
  width: 100%;
  box-shadow: 0 4px 4px 0px rgba(0, 0, 0, 0.25);
  z-index: 99;
}

.appbar.showMenu {
  height: auto;
}

.inner {
  max-width: 1200px;
  position: relative;
  margin: 0 auto;
}

.siteName, .siteName:visited {
  position: absolute;
  display: inline-block;
  margin: 0;
  margin-top: 6px;
  margin-left: 10px;
  text-decoration: none;
}

.siteName img, .siteName:visited img {
  -webkit-filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.5));
  -ms-filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.5));
  filter: drop-shadow(0 0 20px rgba(255, 255, 255, 0.5));
}

.siteLogo {
  display: block;
  height: 96px;
  padding: 0;
}

.lists {
  display: block;
  margin: 0;
  padding: 0;
  width: 100%;
  max-height: 0px;
  overflow: hidden;
  list-style-type: none;
  text-align: left;
}

.lists.showMenu {
  width: 100%;
  max-height: 1000px;
  padding-top: 102px;
}

.lists li {
  display: inline-block;
  margin: 0;
  width: 50%;
  text-align: center;
}

.navItem, .navItem:visited {
  display: block;
  color: #0f0f0f;
  text-decoration: none;
  padding: 11px;
  height: 54px;
}

.navItem:hover, .navItem.active {
  background: rgba(255, 255, 0, 0.5);
}

.navItem > * {
  display: inline-block;
  vertical-align: middle;
}

.lockedNav {
  display: inline-block;
  position: relative;
}

.lockedNav img, .lockedNav span {
  opacity: 0.5;
}

.lockedNav:after {
  content: '即將啟動';
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 14px;
  color: red;
  width: 4em;
  border: 2px solid red;
  padding: 2px;
  background: rgba(255, 255, 255, 0.75);
}

.rightToggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 44px;
  height: 44px;
  cursor: pointer;
}

.icon {
  display: inline-block;
  margin-right: 3px;
}

@media all and (min-width: 400px) {
  .lists li {
    width: 20%;
    vertical-align: top;
  }
  .navItem {
    height: auto;
    padding: 11px 5px;
  }
  .navItem > * {
    display: block;
    margin: 0 auto;
  }
  .navItem > span {
    margin-top: 2px;
  }
}

@media screen and (min-width: 700px) {
  .appbar, .appbar.showMenu {
    height: 76px;
  }
  .lists {
    max-height: 1000px;
    display: block;
    list-style-type: none;
    margin: 0 auto;
    text-align: right;
  }
  .lists li {
    margin: 0;
    width: auto;
    display: inline-block;
  }
  .navItem {
    display: inline-block;
    padding: 11px;
  }
  .rightToggle {
    display: none;
  }
}

@media screen and (min-width: 840px) {
  .appbar, .appbar.showMenu {
    height: 54px;
  }
  .navItem > * {
    display: inline-block;
    margin: 0;
  }
}

.searchImg {
  width: 28px;
  margin: 2px 0;
}



