.innerTag {
  width: 100%;
  margin: 0;
  padding: 0;
  text-align: center;
}

.innerTag li {
  display: inline-block;
  vertical-align: middle;
  float: none;
  cursor: pointer;
}

.innerTag a, .innerTag a:visited {
  display: block;
  padding: 0.5em 1em;
  background: #ddd;
  color: #0f0f0f;
  text-decoration: none;
}

.innerTag a:hover, .innerTag a.active {
  background: #0f0f0f;
  color: #f5f5f5;
}

.notCandidates {
  max-width: 320px;
  margin: 10px auto;
  padding: 10px;
  line-height: 1.4;
  color: #5A5757;
}

/* 承諾 */
.sectionTitle {
  border-bottom: 2px solid;
  display: inline-block;
  font-size: 20px;
  margin: 30px 0 10px 0;
}

.promiseItem {
  margin: 10px;
  border: 2px solid black;
  background: white;
  text-align: left;
  vertical-align: top;
}

@media all and (min-width: 500px) {
  .promiseItem {
    width: 170px;
    display: inline-block;
  }
}

.promiseTitle {
  background: black;
  color: white;
  font-size: 18px;
  padding: 5px;
  border-bottom: 1px solid;
  text-align: center;
}

.promiseQuestion {
  padding: 10px;
  text-align: left;
  min-height: 65px;
  border-bottom: 1px solid;
}

.promisePos {
  width: 100%;
  position: relative;
}

.promiseIcon {
  height: 30px;
  width: 30px;
  margin: 0 10px;
  position: absolute;
  top: 10px;
}

.promisePosText {
  vertical-align: top;
  padding-top: 15px;
  width: 100%;
  text-align: center;
  font-size: 18px;
  font-weight: 600;
}

.promiseStatement {
  text-align: left;
  font-size: 14px;
  padding: 10px;
}

.billItem {
  background: white;
  margin: 10px;
  padding: 20px;
  line-height: 1.6;
  text-align: left;
  box-shadow: 2px 2px 0 0 rgba(0, 0, 0, 0.12);
}

.billItemTitle {
  font-size: 18px;
  font-weight: 600;
  padding: 10px 0;
}

.promiseMeta {
  font-size: 14px;
  margin-top: 40px;
}

.notice {
  margin: 40px auto;
  max-width: 400px;
  text-align: left;
  line-height: 1.4;
  padding: 10px;
}



