.actionText {
  margin: 0 auto 20px auto;
  color: white;
  padding-left: 18px;
  font-size: 14px;
}

@media all and (min-width: 800px) {
  .actionText {
    color: #0f0f0f;
  }
}

.actions {
  line-height: 1.25;
}

.actionButtons {
  font-size: 22px;
  position: relative;
  text-align: center;
  max-width: 420px;
  margin: 40px auto 30px auto;
}

.actionButton {
  display: inline;
  font-size: 24px;
  position: relative;
  margin: 0 4px;
  padding: 8px 20px;
  height: 40px;
  background: #005060;
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.25);
  color: #f5f5f5;
  border-radius: 0;
  cursor: pointer;
}

.actionButton:focus {
  outline: none;
}

.actionButton:hover {
  box-shadow: 2px 2px 2px 0 rgba(0, 0, 0, 0.5);
}

.goMatch, .goMatch:visited {
  cursor: pointer;
  display: inline-block;
  margin: 0 auto;
  background: #005060;
  text-decoration: none;
  color: white;
  padding: 10px 20px;
  box-shadow: 0 0 12px 0 rgba(240, 240, 240, 0.3), 0 0 12px 0 rgba(240, 240, 240, 0.3);
}

.goMatch:hover {
  box-shadow: 2px 2px 32px 0 rgba(240, 240, 240, 0.5), -2px -2px 32px 0 rgba(240, 240, 240, 0.5);
}

.goTable, .goTable:visited {
  color: #f5f5f5;
  border-bottom: 1px solid;
  margin: 20px auto;
  display: inline-block;
  text-decoration: none;
  font-size: 16px;
  font-weight: normal;
  cursor: pointer;
}

@media all and (min-width: 800px) {
  .goTable, .goTable:visited {
    color: #0f0f0f;
  }
}

.wrap {
  position: relative;
  background-size: cover;
  min-height: 100vh;
  text-align: center;
  background-image: url("../images/issues-web.jpg");
  background-position: center center;
  background-size: cover;
}

.wrap.parties {
  background-image: url("../images/parties-embed.jpg");
  background-position: center 160px;
  background-size: cover;
}

.wrap.constituencies {
  background-image: url("../images/constituencies-web.jpg");
  background-position: center 0px;
  background-size: cover;
}

@media all and (min-width: 500px) {
  .wrap.parties {
    background-position: center top;
  }
}

/* 不同 tab 的內容 */
.contentWrap {
  position: relative;
}

.embedSection {
  position: relative;
  background: #ddd;
  box-shadow: 0 0 10px rgba(15, 15, 15, 0.5);
  text-align: left;
}

.embedImg {
  position: absolute;
  top: 0;
  left: 0;
  width: 300px;
  margin: 4px;
}

/* 上方的 tab */
.navWrap {
  text-align: center;
  width: 100%;
  padding-top: 100px;
}

.viewItem {
  cursor: pointer;
  display: inline-block;
  width: 33%;
  padding: 4px;
  border-bottom: 4px solid transparent;
  text-align: center;
  background: #ddd;
}

.viewItem .iconImg {
  display: block;
  width: 32px;
  margin: 0 auto;
}

.viewItem .iconTitle {
  margin: 0;
}

.viewItem.active {
  border-bottom-color: #0f0f0f;
  background: white;
}

@media all and (min-width: 500px) {
  .navWrap {
    text-align: right;
    padding: 0;
  }
  .viewItem {
    display: inline-block;
    width: auto;
    padding: 7px 12px 5px;
  }
}

@media all and (min-width: 840px) {
  .navWrap {
    text-align: center;
  }
}

h1.topic {
  margin: 20px auto;
  font-size: 28px;
}

@media all and (min-width: 750px) {
  h1.topic {
    margin: 20px 0 0 10px;
  }
}

.container {
  margin: 20px auto;
  display: inline-block;
}

.flyingImg {
  width: 250px;
  margin: 0 auto;
}

@media all and (min-width: 750px) {
  .flyingImg {
    position: absolute;
    top: 150px;
    left: calc(50% + 160px);
    z-index: 1;
  }
}

.missionImg {
  display: block;
  width: 55%;
  margin: 0 auto;
  padding: 0 10px;
  cursor: pointer;
  -webkit-filter: drop-shadow(0 0 8px aqua);
  -ms-filter: drop-shadow(0 0 8px aqua);
  filter: drop-shadow(0 0 8px aqua);
}

@media all and (min-width: 750px) {
  .missionImg {
    width: 20%;
    margin: 0 auto;
    display: block;
    padding: 0;
    background: none;
    cursor: pointer;
    -webkit-filter: drop-shadow(0 0 15px aqua);
    -ms-filter: drop-shadow(0 0 15px aqua);
    filter: drop-shadow(0 0 15px aqua);
  }
  .missionImg:hover {
    -webkit-filter: drop-shadow(0 0 8px aqua);
    -ms-filter: drop-shadow(0 0 8px aqua);
    filter: drop-shadow(0 0 8px aqua);
  }
}

/* parties */
.initialWrap {
  padding-top: 40px;
  min-height: 600px;
}

.story {
  max-width: 400px;
  margin: 20px auto 40px auto;
  padding: 10px 20px;
  line-height: 1.6;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.22);
  text-align: left;
}

.story.dark {
  background: rgba(0, 0, 0, 0.8);
  color: white;
}

.story.light {
  background: rgba(250, 250, 250, 0.8);
  color: #0f0f0f;
}

.goImg {
  display: block;
  width: 280px;
  margin: 0 auto;
  -webkit-filter: drop-shadow(0 0 10px white);
  filter: drop-shadow(0 0 10px white);
}

.districtSelector {
  max-width: 400px;
  margin: 0 auto;
}



