.QAItem {
  position: relative;
  display: none;
  margin: 30px 10px;
}

@media screen and (min-width: 400px) {
  .QAItem {
    width: 420px;
    margin: 0 auto;
    padding: 30px;
    position: relative;
    display: none;
  }
}

.QAItem.isActive {
  display: block;
}

.questionContent {
  background: #f7f7f7;
  padding: 20px 10px;
  box-shadow: 3px 3px rgba(0, 0, 0, 0.12);
}

.questionTitle {
  font-size: 20px;
  font-weight: 800;
  padding: 10px;
}

.questionDescription {
  padding: 10px;
  line-height: 1.6;
}

.optionSection {
  margin: 0 auto;
  width: 100%;
  line-height: 1.6;
  text-align: center;
  padding: 50px 0 30px 0;
}

.optionItem {
  margin: 5px;
  padding: 4px 10px;
  cursor: pointer;
  display: inline-block;
  border: 1px solid;
}

@media all and (min-width: 400px) {
  .optionItem {
    padding: 4px 20px;
  }
}

.optionItem.aye {
  border-color: #00FFB0;
  color: #00FFB0;
  background: none;
}

.optionItem.nay {
  border-color: #FF0055;
  color: #FF0055;
  background: none;
}

.optionItem.unknown {
  border-color: #FFF800;
  color: #FFF800;
  background: none;
}

.optionItem.none {
  border-color: gray;
  color: gray;
  background: none;
}

.optionItem.aye:hover, .optionItem.isChoosed.aye {
  color: white;
  background: #00FFB0;
}

.optionItem.nay:hover, .optionItem.isChoosed.nay {
  color: white;
  background: #FF0055;
}

.optionItem.unknown:hover, .optionItem.isChoosed.unknown {
  color: white;
  background: #FF0055;
}

.optionItem.none:hover, .optionItem.isChoosed.none {
  color: white;
  background: gray;
}

.optionItem.isChoosed {
  transition: background 1s;
}



