.wrap {
  position: relative;
  min-height: 500px;
}

.invisibleBlock {
  max-height: 0px;
  overflow: hidden;
}

.interactiveControl {
  position: fixed;
  top: 86px;
  left: 10px;
  width: 80px;
  background: white;
  padding: 10px;
  z-index: 100;
  cursor: pointer;
  border: 1px solid;
}

.headerNotification {
  background: #005060;
  font-size: 14px;
  color: #f5f5f5;
  text-align: center;
  margin: 0;
  padding: 1em;
  line-height: 1.5;
  will-change: max-height;
}

.headerNotificationText {
  width: 100%;
  margin-bottom: 1em;
  text-align: center;
}

.headerNotification.isHide {
  max-height: 0;
  overflow: hidden;
  transition: all 800ms;
  padding: 0;
}

.undo, .removeButton {
  display: inline-block;
  margin: 0;
  padding: 0.25em 0.5em;
  width: calc(50% - 2px);
  background: rgba(255, 255, 255, 0.5);
  color: #0f0f0f;
  cursor: pointer;
}

.undo:hover, .removeButton:hover {
  background: #fff;
}

.undo {
  margin-right: 4px;
}

@media all and (min-width: 400px) {
  .undo, .removeButton {
    width: auto;
  }
}

@media all and (min-width: 900px) {
  .headerNotificationText {
    display: inline-block;
    width: auto;
    margin: 0 1em 0 0;
  }
}

.settingPanel {
  width: 100%;
  padding-top: 8px;
  background: none;
  font-size: 14px;
}

.settingPanel.withBackground {
  background: #fff;
}

.settingButtons {
  width: 100%;
  text-align: right;
}

.settingButton {
  display: inline-block;
  margin: 0 4px;
  cursor: pointer;
  text-align: center;
}



