.wrap {
  display: none;
}

@keyframes emergeBottom {
  0% {
    bottom: -140px;
  }
  50% {
    bottom: 40px;
  }
  100% {
    bottom: 10px;
  }
}

.wrap.show {
  display: block;
  position: fixed;
  bottom: 10px;
  right: 10px;
  padding: 7px;
  background: white;
  text-align: center;
  z-index: 1;
  box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.2);
  animation-fill-mode: forwards;
  transform: translate3d(0, 0, 0);
  animation: emergeBottom 700ms cubic-bezier(0.65, 0.05, 0.36, 1) 1;
}

@keyframes emergeTop {
  0% {
    top: -70px;
  }
  100% {
    top: 120px;
  }
}

@media all and (min-width: 700px) {
  .wrap.show {
    bottom: auto;
    top: 120px;
    animation: emergeTop 740ms cubic-bezier(0.66, -1.74, 0, 2.24) 1;
  }
}

.closeIcon {
  position: absolute;
  top: -15px;
  left: -15px;
  width: 40px;
  cursor: pointer;
}

.mainImg {
  width: 150px;
}

.title, .title:visited {
  font-size: 18px;
  color: white;
  text-decoration: none;
  display: block;
  cursor: pointer;
  background: #62CCBA;
  padding: 2px;
}

.title:hover {
  color: black;
}



