@charset "UTF-8";
.wrap {
  position: relative;
  max-width: 400px;
  margin: 0 auto;
}

@media all and (min-width: 800px) {
  .wrap {
    display: none;
  }
}

.embedWrap {
  /* EMBED 專用 */
  position: relative;
}

select.selector {
  display: block;
  width: 100%;
  margin: 10px 0;
  background-color: #D0D3D5;
  border-radius: 0px;
  border: 0;
  height: 40px;
  font-size: 20px;
}

.option {
  width: 100%;
}

.buttonWrap {
  text-align: center;
}

.button, .button:visited {
  margin: 0 auto;
  display: inline-block;
  padding: 4px 20px;
  font-size: 20px;
  border: none;
  color: white;
  background: #026466;
  text-decoration: none;
}

@media all and (min-width: 800px) {
  .wrap {
    display: none;
  }
}



