/* ------------- */
.home {
  position: relative;
}

.invisible {
  max-height: 0;
  overflow: hidden;
}

.contentWrap {
  padding: 60px 0 0;
  border-top: 1px solid #e8f6fb;
}

.innerWrap {
  width: 100%;
  margin: 0 auto;
}

.stonehenge {
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
}

.sky {
  width: 100%;
  padding: 300px 0 80px;
  background-image: url(./images/sky.png);
  background-size: cover;
  background-position: center center;
  text-align: center;
  cursor: pointer;
}

.sky .stargate {
  position: relative;
  display: inline-block;
  transition: transform 0.5s ease;
  padding-top: 100px;
  -webkit-transform: scale(1.1);
  -ms-transform: scale(1.1);
  transform: scale(1.1);
}

.sky .stargate, .sky .stargate:active, .sky .stargate:visited {
  text-decoration: none;
  color: #0f0f0f;
}

.sky .stargate.locked > .destination {
  -webkit-filter: grayscale(100%);
  filter: grayscale(100%);
}

.sky .stargate .destination {
  display: block;
  width: 320px;
}

.sky .stargate .glow {
  position: absolute;
  top: 100px;
  left: 0;
  width: 320px;
}

.sky .stargate .label_issues, .sky .stargate .label_parties, .sky .stargate .label_constituencies {
  position: absolute;
  top: -25px;
  left: 50%;
  -webkit-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 125px;
  height: 200px;
  background-image: url("./images/labels.svg");
  background-size: cover;
  background-position: 0 0;
}

.sky .stargate .label_parties {
  background-position: -130px 0;
  width: 175px;
}

.sky .stargate .label_constituencies {
  background-position: -300px 0;
  width: 175px;
}

.countdown {
  display: block;
  position: absolute;
  top: 100px;
  left: 50%;
  transform: translateX(-50%);
}

@media all and (min-width: 960px) {
  .countdown {
    top: 120px;
  }
  .stargate:hover {
    transform: scale(1.35);
    -webkit-filter: drop-shadow(0 0 20px rgba(255, 255, 0, 0.5));
    filter: drop-shadow(0 0 20px rgba(255, 255, 0, 0.5));
  }
}



