@import '../../common.scss';
@import '../../styles/actionButton.scss';
$rollLargeBreak: 800px;
.wrap {
	min-height: 60vw;
	background-image: url('../images/parties-mobile.jpg');
}
@media all and (min-width: $rollLargeBreak){
	.wrap {
		background: none;
	}
	.bgHolder {
	 	left: 0px;
	 	right: 0px;
	 	bottom: 0px;
	 	top: 0px;
	 	position: fixed;
	 	background-image: url('../images/parties-web.jpg');
	 	background-size: cover;
	 	transform: translateZ(0);
	 	pointer-events: none;
	 	z-index: -1;
	}
}

/* 說明 */
.initialWrap {
	padding-top: 40px;
	min-height: 600px;
}
.story {
	max-width: 400px;
    margin: 0px auto 40px auto;
    padding: 20px;
    line-height: 1.6;
    background: rgba(0,0,0,0.8);
    color: white;
    box-shadow: 2px 2px 10px 0 rgba(0, 0, 0, 0.22);
}
.actions {
	text-align: center;
}
.billWrap {
	padding-bottom: 120px;
}
.billComplete {
	text-align: center;
	font-size: 22px;
	font-weight: 600;
	margin-top: 40px;
	> img {
		width: 200px;
	}
}
/* ProgressBar */
.stageWrap {
	background: rgba($awesomeColor, 0.6);//rgba(0,0,0,0.8);
	text-align: center;
	padding-top: 20px;
	margin-bottom: 20px;
}
@media all and (min-width: 840px){/* appbar */
	.stageWrap {
		margin-top: 0;//-1px;
		padding-top: 0;
	}
}
$lockColor: $awesomeColor;//gray;
$completeColor: $white;//#FAFA7B;
$strokeWright: 2px;
.sItem {
	display: inline-block;
	vertical-align: top;
	padding: 10px;
	position: relative;
	font-size: 0.85em;
	color: $lockColor;

	&:not(:first-child):after {
		content: '';
		position: absolute;
		top: 18px;
		right: 50%;
		width: 100%;
		height: $strokeWright;
		background: $lockColor;
	}
	&.current {
		font-size: 1em;
		color: $completeColor;
		z-index: 1;
		&:after {
			background: $completeColor;
		}
	}
	&.complete {
		color: $completeColor;
		&:after {
			background: $completeColor;
		}
	}
}

.circle {
	width: 10px;
	height: 10px;
	margin: 4px auto;
	border-radius: 100%;
	background: $lockColor;

	&.complete {
		background: $completeColor;
	}
	&.current {
		background: $completeColor;
		width: 16px;
		height: 16px;
		margin-top: 1px;
	}
}



